$alert: "alert";

.#{$alert} {
    display: flex;
    align-items: stretch;
    padding: 1rem 2rem;
    margin: 0 0 20px 0;
    font-size: 0.85rem;
    border-radius: $common-radius;
    &-icon {
        display: flex;
        align-items: center;
        padding-#{$end-direction}: 1rem;
        &-icon-top {
            align-items: flex-start;
            align-content: stretch;
        }
        i {
            font-size: 2rem;
        }
    }
    &-text {
        align-self: center;
        flex-grow: 1;
    }
    &-close {
        padding: 0 0 0 1rem;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        .close {
            text-shadow: none !important;
        }
        i {
            opacity: 0.8;
            transition: all 0.3s ease;
            cursor: pointer;
            &:hover {
                opacity: 1;
            }
        }
    }
    &-elevate {
        box-shadow: 0 0 13px 0 rgba(82, 63, 105, 0.1);
    }
    &-brand {
        background: #366cf3;
        border: 1px solid #366cf3;
        color: #fff;
        &-icon {
            i {
                color: #fff;
            }
        }
        &-text {
            color: #fff;
            hr {
                border-top-color: #fff;
                opacity: 0.4;
            }
        }
        &-close {
            i {
                color: #fff;
            }
        }
    }
    &-outline-brand {
