    &-secondary {
        &-text {
            color: #595d6e;
        }
    }
    &-bold {
        &-text {
            font-weight: 500;
        }
    }
    &--marginless {
        margin: 0;
    }

    &-intru {
        border: $border;
        border-radius: 3px;
    }

    &[data-notify] {
        min-width: 300px;
        .close {
            right: 10px !important;
            padding: 0.25rem 0 0 2rem;
            font-family: LineAwesome;
            text-decoration: inherit;
            text-rendering: optimizeLegibility;
            text-transform: none;
            -moz-osx-font-smoothing: grayscale;
            font-weight: 300;
            &:before {
                content: "";
                font-size: 1.1rem;
                color: #fff;
                opacity: 0.7;
                transition: all 0.3s;
            }
            &:hover {
                &:before {
                    opacity: 1;
                    transition: all 0.3s;
                }
            }
        }
    }
    @media (min-width: 769px) and (max-width: 1024px) {
        &[data-notify] {
            max-width: 70%;
        }
    }
    @media (max-width: 768px) {
        &[data-notify] {
            max-width: 90%;
        }
    }
    &[data-notify][data-notify-position="bottom-center"],
    &[data-notify][data-notify-position="top-center"] {
        width: 30%;
    }
    @media (min-width: 769px) and (max-width: 1024px) {
        &[data-notify][data-notify-position="bottom-center"],
        &[data-notify][data-notify-position="top-center"] {
            width: 70%;
        }
    }
    @media (max-width: 768px) {
        &[data-notify][data-notify-position="bottom-center"],
        &[data-notify][data-notify-position="top-center"] {
            width: 90%;
        }
    }
    &[data-notify] .icon {
        position: absolute;
    }
    &[data-notify] [class*=" la-"],
    &[data-notify] [class^="la-"] {
        font-size: 1.8rem;
    }
    &[data-notify] [class*=" fa-"],
    &[data-notify] [class^="fa-"] {
        font-size: 1.6rem;
    }
    &[data-notify] [class*=" flaticon-"],
    &[data-notify] [class*=" flaticon2-"],
    &[data-notify] [class^="flaticon-"],
    &[data-notify] [class^="flaticon2-"] {
        font-size: 1.8rem;
    }
    &[data-notify] [class*=" la-"],
    &[data-notify] [class^="la-"] {
        margin-top: -0.1rem;
    }
    &[data-notify] [class*=" fa-"],
    &[data-notify] [class^="fa-"] {
        margin-top: -0.1rem;
    }
    &[data-notify] [class*=" flaticon-"],
    &[data-notify] [class*=" flaticon2-"],
    &[data-notify] [class^="flaticon-"],
    &[data-notify] [class^="flaticon2-"] {
        margin-top: -0.4rem;
    }
    &[data-notify] [data-notify="title"] {
        display: block;
        font-weight: 500;
    }
    &[data-notify] .icon ~ [data-notify="title"] {
        padding-#{$start-direction}: 2.85rem;
    }
    &[data-notify] .icon ~ [data-notify="message"] {
        display: inline-block;
        padding-#{$start-direction}: 2.85rem;
    }
    &[data-notify] [data-notify="title"]:not(:empty) ~ [data-notify="message"] {
        margin-top: 0.2rem;
    }
    &[data-notify] .progress {
        margin-top: 0.5rem;
        line-height: 0.5rem;
        height: 0.5rem;
    }
}
