  .wave {
    position: absolute;
    width: 1000px;
    height: 1000px;
    animation: wave 8s linear infinite;
    background-color: #e7f0fd;
    border-radius: 35%;
    top: 90%;
    left: -94%;
    @keyframes wave {
      from {
        transform: rotate(0deg);
      }
      from {
        transform: rotate(180deg);
      }
    }
  }
  .wave-content {
    position: relative;
    padding: 30px 40px;
