  text-align: #{$start-direction};
}
.accounts-nav {
  display: flex;
  align-items: center;
  > li {
    list-style: none;
    > a {
      background: transparent;
      border: none;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      margin: 0 auto;
      padding: 10px;
      > &:hover {
        background-color: rgba(0, 0, 0, 0.1);
      }
    }
  }
  &_avatar {
    width: 42px;
    height: 42px;
    border-radius: 4px;
  }
}
.dropdown-toggle {
  &.no-after {
    &::after {
      display: none;
    }
  }
}
.modal {
  &-content {
    border-radius: 4px;
    overflow: hidden;
  }
}
.text-link {
  font-size: 0.875rem;
  color: #7e8299;
  position: relative;
  &--arrow {
    position: relative;
    padding-#{$end-direction}: 20px;
    &:hover {
      text-decoration: none;
    }
    &::before {
      position: absolute;
      #{$end-direction}: 5px;
      top: 7px;
      content: "";
      height: 7px;
      width: 7px;
      border-#{$end-direction}: 1px solid currentColor;
      border-top: 1px solid currentColor;
      transform: rotate(45deg);
    }
    &:after {
      position: absolute;
      #{$end-direction}: 5px;
      top: 10px;
      content: "";
      height: 1px;
      width: 10px;
      background-color: currentColor;
    }
  }
}
.text-xs {
  font-size: 0.8rem;
}
.palette {
  // background-color: $bg-gray;
  // border-radius: $common-radius;
  // padding: 10px 0;
  // padding-bottom: 0;
}
.color-data {
  display: flex;
  align-items: center;
  font-size: 0.8rem;
  .color-swatch {
    input {
      padding: 0;
      outline: none;
      border: none;
      background: none;
      height: 40px;
      border-radius: 4px;
    }
  }
  .color-label {
    min-width: 110px;
    padding: 0 1rem;
  }
  h5 {
    margin: 0;
    font-size: 0.85rem;
  }
}
//list
.list-bullet {
  li {
    color: $txt-gray;
    font-size: 0.85rem;
    margin: 0.5rem 0;
    list-style-position: inside;
    &::marker {
      color: $brand-color;
    }
  }
}
.list-chips {
  display: flex;
  align-items: center;
  li {
    display: inline-block;
    background-color: $bg-gray;
    border-radius: 20px;
    padding: 0.5rem 1rem;
    margin-#{$end-direction}: 1rem;
    a {
      display: flex;
      align-items: center;
      .svg {
        width: 12px;
        height: 12px;
        margin-#{$end-direction}: 10px;
      }
    }
  }
}
.list-stats {
  word-break: break-word;
  font-size: 0.85rem;
  min-width: 250px;
  list-style: none;
  &-item {
    list-style: none;
    padding: 0.8rem 0;
    display: flex;
    gap: 10px;
    .label {
      color: $txt-gray;
      padding: 0;
      margin-bottom: 0.5rem;
    }
    .value {
      font-weight: 500;
      color: $txt-body;
      //white-space: break-spaces;
      //font-size: 1rem;
    }
    &:first-child {
      padding-top: 0;
    }
    &:last-child {
      padding-bottom: 0;
    }
    &-full {
      flex: 100% !important;
      max-width: 100% !important;
    }
  }
  .icn {
    font-size: 11px;
  }
  .dot {
    background-color: $bg-gray;
    display: block;
    min-width: 8px;
    width: 8px;
    height: 8px;
    border-radius: 2px;
    margin-#{$end-direction}: 8px;
  }
  &-inline {
    li {
      flex-direction: row;
      justify-content: space-between;
      border-bottom: $border;
      &:last-child {
        border-bottom: none;
      }
    }
    .label {
      display: flex;
      align-items: center;
    }
  }
  &-double {
    display: flex;
    flex-wrap: wrap;
    li {
      padding: 0;
      padding-bottom: 1rem;
      flex: 0 0 50%;
      max-width: 50%;
    }
  }
  &-triple {
    display: flex;
    flex-wrap: wrap;
    li {
      padding: 0;
      padding-bottom: 1.5rem;
      flex: 0 0 30%;
      max-width: 30%;
    }
  }
  &-popover {
    li {
      display: flex;
      gap: 10px;
      flex-direction: row !important;
    }
  }

  &-orders {
    li {
      display: flex;
      flex-flow: row wrap;
      justify-content: space-between;
      padding: 5px 0;
      border-top: $border;
      color: $txt-gray;
      font-size: 0.8rem;
      &:first-child {
        border-top: 0;
      }
    }

    .value {
      text-align: #{$end-direction};
    }
    i {
      font-style: normal;
      color: $txt-gray;
      opacity: 0.5;
    }
  }
}
.list-checkbox {
  display: flex;
  flex-direction: column;
  .checkbox {
    display: block;
    padding: 0.8rem 1rem;
  }
}
.list-popover {
  list-style: none;
  &-item {
    padding: 4px 0;
    display: flex;
    justify-content: space-between;
  }
  .value {
    font-weight: 600;
  }
}
.or {
  position: relative;
  text-transform: uppercase;
  font-size: 11px;
  color: $txt-gray;
  margin: 30px 0;
  display: flex;
  text-align: center;
  &:after {
    content: "";
    height: 1px;
    width: 100%;
    background: $border-color;
    position: absolute;
    top: 50%;
    left: 0;
  }
  span {
    position: relative;
    z-index: 1;
    background: $bg-white;
    padding: 0 5px;
    letter-spacing: 2px;
    margin: 0 auto;
  }
}
.custom-drag-drop {
  display: inline-block;
  .btn {
    //position: static !important;
  }
  .scroll {
    max-height: 300px;
  }
  .list-drag-drop {
    padding: 1rem;
    display: flex;
    flex-direction: column;
    li {
      display: flex;
      align-items: center;
      border: dotted 1px transparent;
      margin: 0;
      &:hover {
        /*background-color: var(--bg-gray);*/
        border-color: $border-color;
      }
      .svg {
        cursor: grab;
        margin-#{$start-direction}: 10px;
      }
    }
  }
  .checkbox {
    font-size: 0.75rem;
    color: $txt-gray;
    white-space: nowrap;
    text-overflow: ellipsis;
    display: block;
    flex: 1;
    max-width: 256px;
    overflow: hidden;
    &.disabled {
      opacity: 0.75;
      pointer-events: none;
      input {
        &:checked {
          background-color: $border-color;
          border-color: $border-color;
        }
      }
    }
  }
  .ui-sortable-helper {
    background-color: $bg-brand-light;
    color: $brand-color;
    border-color: $bg-brand-light !important;
  }
}
.cart-summary {
  font-size: 0.85rem;
  ul {
    li {
      padding: 0.8rem 0;
      border-left: 0;
      border-right: 0;
      border-radius: 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .label {
        color: currentColor;
        padding: 0;
      }
      .value {
        font-weight: 500;
        text-align: #{$end-direction};
        margin-#{$start-direction}: auto;
      }
      &.highlighted {
        font-weight: 500;
        border-top: $border;
        color: $txt-body;
        margin-top: 1rem;
        padding-top: 1rem;
      }
      &.text-success {
        color: $success-color;
      }
      &:first-child {
        padding-top: 0;
      }
      &:last-child {
        padding-bottom: 0;
      }
    }
  }
}
.dropdown-toggle-custom {
  cursor: pointer;
  border: none;
  position: relative;
  &-arrow {
    position: absolute;
    transform: translate(-6px, 0);
    top: calc(50% - 4px);
    //#{$end-direction}: 25px;
    #{$end-direction}: 0;
    &:after,
    &:before {
      content: "";
      position: absolute;
      background-color: currentColor;
      width: 1px;
      height: 9px;
      transition: all 0.25s ease-in-out;
    }
    &:before {
      transform: translate(3px, 0) rotate(45deg);
    }
    &:after {
      transform: translate(-3px, 0) rotate(-45deg);
    }
  }
  &:not(.collapsed) {
    .dropdown-toggle-custom-arrow {
      &:before {
        transform: translate(-3px, 0) rotate(45deg);
      }
      &:after {
        transform: translate(3px, 0) rotate(-45deg);
      }
    }
  }
}
.card-toggle {
  .dropdown-toggle-custom-arrow {
    position: relative;
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: none;
  }
}
.users-addresses {
  display: flex;
  li {
    flex: 0 0 33%;
    max-width: 33%;
    padding: 0;
    + li {
      padding-#{$start-direction}: 1.5rem;
    }
  }
}
.card-aside {
  @include media-breakpoint-down(lg) {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: $zindex-offcanvas;
    transition: transform 0.3s ease-out;
    transform: translateX(100% * $transform-direction);
  }
  .card-head {
    flex-direction: row;
    @include media-breakpoint-up(lg) {
      display: none !important;
    }
  }
  &.show {
    transform: translateX(0 * $transform-direction);
  }
}
.float-btn {
  will-change: transform, opacity;
  border: none;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.14), 0 4px 8px rgba(0, 0, 0, 0.28);
  overflow: hidden;
  // background-color: $brand-color;
  // color:  lighten($brand-color, 55%);
  @include bg-brand();
  border-radius: 50%;
  width: 45px;
  height: 45px;
  position: fixed;
  margin: 1em;
  z-index: $zindex-offcanvas;
  #{$end-direction}: 0.5rem;
  bottom: 6rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.1s ease-out;
  @include media-breakpoint-up(lg) {
    display: none;
  }
}
.txt-description {
  display: inline-flex;
  white-space: nowrap;
  min-width: 190px;
  .btn-view {
    background-color: $bg-gray;
    border: $border;
    padding: 0;
    margin: 0;
    margin-#{$start-direction}: 5px;
    color: $txt-body;
    width: 1.8rem;
    height: 1rem;
    border-radius: 3px !important;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

//
.grid-layout {
  background-color: #fff;
  @include media-breakpoint-up(lg) {
    height: 100%;
    display: grid;
    // grid-gap: 2rem;
    grid-template-columns: minmax(auto, 320px) 2fr;
    &-left {
      border-#{$end-direction}: $border;
    }
  }
  &.theme-layout {
    grid-template-columns: minmax(auto, 400px) 2fr;
  }

  $card-border: 0;
  .card {
    border: $card-border;
  }
}
.side-nav {
  display: flex;
  flex-direction: column;
  &-item {
    list-style: none;
    margin: 0.2rem 0;
  }
  &-link {
    padding: 0.8rem 1rem;
    display: flex;
    align-items: center;
    gap: 1rem;
    font-size: 0.95rem;
    font-weight: 500;
    color: $txt-gray;
    border-radius: $radius;
    &:hover,
    &[data-status="current"] {
      background-color: $bg-gray;
      .svg {
        g [fill] {
          fill: $brand-color;
        }
      }
    }
  }

  .svg {
    g [fill] {
      fill: #a7abc3;
    }
  }
}
.edit-price {
  display: inline-flex;
  flex-direction: column;
  .click-to-edit {
    margin-bottom: 1rem;
  }
}
.verified-wrap {
  display: flex;
  margin-bottom: 5px;
  //align-items: center;
}
.verified {
  position: absolute;
  top: -6px;
  #{$end-direction}: 0;
  color: $brand-color;
  filter: grayscale(1);
  opacity: 0.5;
  &.is-verified {
    filter: none;
    opacity: 1;
  }
} //plugin

.custom-select2 {
  .select2-selection__clear {
    font-size: 1rem !important;
  }
  .select2-search__field {
    margin: 0;
    padding: 0;
    border: $border;
    height: 40px;
    appearance: none;
    padding-#{$start-direction}: 40px;
    background-image: url("data:image/svg+xml;base64,");
    background-size: 14px;
    background-repeat: no-repeat;
    background-position: calc(13px) center;
    background-color: $bg-gray;
    border: none;
    margin: 0;
    &:focus,
    &:hover,
    &:focus-visible {
      outline: none !important;
      border: none !important;
    }
    .main[dir="rtl"] & {
      padding-#{$end-direction}: 40px;
    }
    [dir="rtl"] & {
      background-position: calc(100% - 13px) center;
    }
  }
  .select2-results__option--highlighted {
    background-color: $brand-color !important;
    color: $brand-color-inverse !important;
    cursor: pointer !important;
  }
}
.custom-select2-width {
  width: 100% !important;
}
.custom-select2-single {
  &.error {
    border: 1px solid $danger-color;
    background-color: transparent;
  }
  .select2-dropdown {
    //padding: 1rem;
    border: $border !important;
    border-radius: $common-radius !important;
  }
  .select2-search--dropdown {
    margin: 1rem;
    padding: 0;
  }
} //phone code
.iti {
  // width: 100%;
  //height: $input-height;
  font-size: 0.8rem;
  position: relative;
  &__selected-flag {
    background-color: $input-bg;
    border-radius: $input-border-radius;
    // margin-#{$end-direction}: 10px !important;
    position: absolute;
    .form[dir="rtl"] & {
      margin-#{$start-direction}: 10px !important;
      margin-#{$end-direction}: auto !important;
    }
  }
  &__country-list {
    position: relative;
    white-space: pre-wrap;
    width: 100%;
    top: 100%;
  }
  &[dir="ltr"] {
    .iti__flag-container {
      right: auto !important;
      left: 0 !important;
    }
    .iti__country-list {
      left: 0;
      text-align: left;
    }
  }
  &[dir="rtl"] {
    .iti__flag-container {
      right: 0 !important;
      left: auto !important;
    }
    .iti__country-list {
      text-align: right;
      right: 0;
    }
  }
}
.ui-datepicker {
  // z-index: $zindex-dropdown !important;
}
.dropzone {
  .dz-preview {
    margin: 0 !important;
    padding: 1.25rem 0.5rem;
    min-height: 100px;
    background: linear-gradient(to bottom, #efefef, #c9c9c9);
    border-radius: 1rem;
    min-width: 160px;
    width: 160px;
    .dz-progress {
      width: 100% !important;
      left: 0 !important;
      right: 0;
      margin: auto !important;
      height: 8px !important;
      margin-top: 8px !important;
      .dz-upload {
        background: $brand-color !important;
      }
    }
    .dz-image {
      display: none !important;
    }
    .dz-details {
      padding: 1rem !important;
    }
    .dz-details,
    .dz-progress-text,
    .dz-progress {
      position: relative !important;
    }
    .dz-remove {
      margin-top: 10px;
    }
  }
}
.lbl-link {
  display: flex !important;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}
.unit-price-wrap {
  display: flex;
  grid-gap: 10px;
  font-weight: 600;
}
.offcanvas-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1040;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
}
.head-block {
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  max-width: 800px;
  margin: 0 auto;

  h1 {
    color: #000;
  }
  p {
    color: $txt-gray;
  }
}

.post {
  height: 100%;
  position: relative;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  &-body {
    flex: 1;
    display: flex;
    flex-direction: column;
    padding-top: 1.5rem;
  }
  &-foot {
    margin-top: 1rem;
    padding-bottom: 1.5rem;
  }
  &-media {
    position: relative;
    margin: 0;
    padding-bottom: 56.25%;
    background-color: $bg-placeholder;
    /* 16:9, for an aspect ratio of 1:1 change to this value to 100% */

    img {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
    iframe {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }

  &-category {
    display: flex;
    //margin-bottom: 1rem;
    flex-wrap: wrap;
    &-tag {
      a {
        display: block;
        font-size: 0.8rem;
        line-height: 1.2;
        color: $txt-gray;
        /*text-transform: uppercase;*/
        padding: 5px 0;
        margin-bottom: 10px;
        margin-#{$end-direction}: 10px;
      }
    }
  }

  &-title {
    line-height: 1.5;
    font-weight: 600;
    font-size: 1.3rem;
    // margin-bottom: 1.5rem;
    color: $txt-body;
    flex: 1;
    a {
      color: inherit;
    }
  }

  &-description {
    color: $txt-gray;
    line-height: 1.7;
    margin-bottom: 2rem;
  }

  &-link {
    color: $brand-color;
    font-size: 1rem;
    &:after {
      transition: all 0.5s ease-out;
      padding-#{$start-direction}: 4px;
      content: "\2192";
    }
    &:hover {
      text-decoration: none;
      &:after {
        padding-#{$start-direction}: 8px;
      }
    }
  }
}
.article-inner {
  .blog_author {
    font-size: 0.8rem;
    margin-bottom: 0.8rem;
    .article__author {
      color: $txt-body;
      margin-#{$end-direction}: 10px;
      font-weight: 600;
    }
    .article__date {
      color: lighten($txt-body, 50%);
    }
  }
  .article-title {
    margin-bottom: 1rem;
    line-height: 1.5;
    font-weight: 600;
    font-size: 1.25rem;
  }
  a {
    color: inherit;
    text-decoration: none;
    &:hover {
      text-decoration: underline;
      color: inherit;
    }
  }
}

//quick-search
.quick-search {
  &-form {
    margin-bottom: 2rem;
  }
  .sidebar-nav {
    li {
    }
  }
}

//Site map
.site-map {
  .sidebar-menu {
    width: auto;
    overflow: inherit;
  }
  .sidebar-nav {
    justify-content: flex-start;
    list-style: none;
    flex-flow: row wrap;
    > .sidebar-nav-item {
      flex: 0 0 14%;
      max-width: 14%;
    }
  }
  .dropdown-toggle-custom-arrow {
    display: none;
  }
  > .dropdown-toggle-custom {
    pointer-events: none;
  }
  .collapse:not(.show) {
    display: block !important;
  }
}
