#particles-js {
    position: absolute;
    width: 100%;

    background: $brand-color;
    /*	background-color: #FF3A59;*/
    position: fixed;
    z-index: -1;
    background: $brand-color;
    /* fallback for old browsers */
    /* Chrome 10-25, Safari 5.1-6 */
    //background: linear-gradient(to right, darken($brand-color, 10%), lighten($brand-color, 5%));
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    [data-theme="dark"] & {
        background: #121212;
    }
}
