.table {
    color: $txt-body;
    width: 100%;
    margin: 0;
    //margin-bottom: 1rem;
    background-color: transparent;
    font-size: 0.85rem;
    //word-break: break-word;
    thead {
        th {
            // position: sticky;
            // top: 0;
            // z-index: 9999;
            // background-color: #fff;
            white-space: nowrap;
            color: $txt-body;
            font-weight: 600;
            //text-transform: uppercase;
            border-bottom: solid 1px lighten($border-color, 05) !important;
            &:first-child {
                //padding-#{$start-direction}: 0;
            }
            &:last-child {
                //padding-#{$end-direction}: 0;
            }

            &.col-sr {
                width: 25px;
            }
        }
    }
    tbody {
        + {
            tbody {
            }
        }
        tr {
            &:last-child td {
                border-bottom: none;
            }
        }

        a {
            color: currentColor;
        }
    }
    tfoot {
    }
    th,
    td {
        padding: 1rem $card-space;
        vertical-align: top;
        border-top: solid 1px lighten($border-color, 05);
        color: $txt-gray;
    }
    tr {
        td {
            .checkmark {
                filter: grayscale(1);
                &.is-check {
                    filter: none;
                }
                img {
                    width: 24px;
                    height: 24px;
                }
            }

            &:first-child {
                //padding-#{$start-direction}: 0;
            }
            &:last-child {
                //padding-#{$end-direction}: 5px;
            }
        }
        &:hover {
            background-color: lighten($bg-gray, 2);
        }
        &.selected {
            background-color: $bg-gray;
        }
    }
    .col-check {
        width: 20px;
        padding-#{$end-direction}: 0;
    }
    & {
        .thead-dark {
            th {
                color: #fff;
                background-color: $gray-800;
                border-color: #454d55;
            }
        }
        .thead-light {
            th {
                color: #6c7293;
                background-color: #ebedf2;
                border-color: #ebedf2;
            }
        }
        thead {
            th {
                font-weight: 500;
                border-bottom-width: 1px;
                padding-top: 1rem;
                padding-bottom: 1rem;
            }
            td {
                font-weight: 500;
                border-bottom-width: 1px;
                padding-top: 1rem;
                padding-bottom: 1rem;
            }
        }
        &:not(&-bordered) {
            thead {
                th {
                    border-top: 0;
                }
                td {
                    border-top: 0;
                }
            }
        }
    }
    &-sm {
        th {
            padding: 0.3rem;
        }
        td {
            padding: 0.3rem;
        }
    }
    &-variants {
        table-layout: fixed;

        thead th {
            &:first-child {
                padding-#{$start-direction}: 1.5625rem;
            }
            &:last-child {
                padding-#{$end-direction}: 1.5625rem;
            }
        }
        tbody td {
            &:first-child {
                padding-#{$start-direction}: 1.5625rem;
            }
            &:last-child {
                padding-#{$end-direction}: 1.5625rem;
            }
        }

        td,
        th {
            padding: 1rem 5px;
        }
    }
    &-slots {
        thead th {
            &:first-child {
                padding-#{$start-direction}: 0;
            }
            &:last-child {
                padding-#{$end-direction}: 0;
            }
        }
        tbody td {
            &:first-child {
                padding-#{$start-direction}: 0;
            }
            &:last-child {
                padding-#{$end-direction}: 0;
            }
        }
    }
    &-zebra {
        font-size: 0.85rem;
        table-layout: fixed;
        tr {
            box-shadow: $shadow0;
            margin: 0.5rem 0;
            background: #fff;
            display: inline-table;
            width: 100%;
            border-radius: $radius3;
        }
        thead {
            th {
                color: $txt-body;
                font-weight: 500;
                padding: 1rem 0.8rem;
            }
        }
        tbody {
            color: $txt-gray;
            tr {
                td {
                    padding: 0.8rem 0.8rem;
                }
            }
        }
    }
    &-bordered {
        border: $border;
        th {
            border: $border;
        }
        td {
            border: $border;
        }
        thead {
            th {
                border-bottom-width: 2px;
            }
            td {
