.data-length {
    font-size: 0.85rem;
    display: flex;
    align-items: center;
    &-select {
        max-width: 80px;
        margin-#{$end-direction}: 1.5rem;
    }
}

.actions {
    display: inline-flex;
    align-items: center;
    justify-content: flex-end;
    vertical-align: top;
    white-space: nowrap;
    margin: 0;
    > li {
        display: inline-block;
        vertical-align: top;
        //margin-#{$end-direction}: 10px;
        &:last-child {
            margin-#{$end-direction}: 0;
        }
        > a {
            //background: lighten($bg-gray, 0.1);
            //color: $brand-color-inverse;
            // width: 30px;
            // height: 30px;
            position: relative;
            z-index: 0;
            color: $txt-gray;
            text-align: center;
            display: flex;
            align-items: center;
            justify-content: center;
            text-decoration: none;
            //border: $input-border;
            height: $input-height;
            width: $input-height;
            padding: 0.5rem;
            font-size: $input-font-size;
            line-height: $input-line-height;
            border-radius: $input-border-radius;
            &::before {
                background: lighten($brand-color, 55);
                content: "";
                height: 20%;
                width: 20%;
                // border-radius: $common-radius;
                border-radius: $common-radius;
                position: absolute;
                z-index: -1;
                margin: auto;
                opacity: 0;
                transition: all 0.2s cubic-bezier(0.2, 0.91, 0.85, 0.96) 0s;
                &:focus {
                    outline: none;
                }
            }
            &:hover {
                color: $brand-color;
                &::before {
                    opacity: 1;
                    width: 100%;
                    height: 100%;
                }
            }
            .svg {
                // width: 16px;
                // height: 16px;
                [fill]:not(.permanent):not(g) {
                    transition: fill 0.3s ease;
                    // fill: $brand-color;
                }
            }
            &.disabled {
                opacity: 0.65;
                cursor: not-allowed;
            }
        }
    }
}

.sorting {
    cursor: pointer;
    position: relative;
    > span {
        position: relative;
        display: inline-flex;
        align-items: center;
        .svg {
            margin-#{$start-direction}: 4px;
            width: 12px;
            height: 12px;
        }
        &:before,
        &:after {
            position: absolute;
            display: block;
            opacity: 0.5;
        }
        &:before {
            content: "\2191";
            #{$end-direction}: -12px;
            [dir="rtl"] & {
                #{$start-direction}: -12px;
                #{$end-direction}: auto;
            }
        }
        &:after {
            content: "\2193";
            #{$end-direction}: -17px;
            [dir="rtl"] & {
                #{$start-direction}: -17px;
                #{$end-direction}: auto;
            }
        }
    }
    &_asc {
        > span {
            color: $brand-color;
            &:before {
                opacity: 1;
            }
        }
    }
    &_desc {
        > span {
            color: $brand-color;
            &:after {
                opacity: 1;
            }
        }
    }
    &:hover {
        > span {
            color: $brand-color;
        }
    }
}

.table-processing {
    background-color: rgba(255, 255, 255, 0.55);
    position: absolute;
    z-index: 1024;
    top: 0;
    bottom: 0;
    #{$start-direction}: 0;
    #{$end-direction}: 0;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    &:after {
        position: absolute;
        content: "";
        background-color: rgba(255, 255, 255, 0.05);
        top: 0;
        bottom: 0;
        #{$start-direction}: 0;
        #{$end-direction}: 0;
        z-index: 1;
    }
}

.date {
    white-space: nowrap;
    time {
        display: block;
    }
}

.info-wrap {
    min-width: 200px;
}
